import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuardService } from './services/guards/auth-guard.service';
import { DefaultLayoutComponent } from './shared/layouts/default/default-layout.component';

const routes: Routes = [
  {
    path: '',
    component: DefaultLayoutComponent,
    loadChildren: () => import('./pages/login/login.module').then(response => response.LoginModule)
  },
  {
    path: 'user',
    component: DefaultLayoutComponent,
    loadChildren: () => import('./pages/user/user.module').then((response) => response.UserModule),   
    canActivate: [AuthGuardService]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
