import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HeaderComponent } from './shared/header/header/header.component';
import { FooterComponent } from './shared/footer/footer/footer.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SharedModule } from './shared/shared.module';
import { DefaultLayoutComponent } from './shared/layouts/default/default-layout.component';
import { ExtractComponent } from './components/user/extract/extract.component';
import { CalendarComponent } from './components/user/calendar/calendar.component';
import { LoginComponent } from './pages/login/login.component';
import { AuthGuardService } from './services/guards/auth-guard.service';
import { BuyComponent } from './components/user/buy/buy.component';

@NgModule({
  declarations: [
    AppComponent,
    DefaultLayoutComponent,
    HeaderComponent,
    FooterComponent,
    ExtractComponent,
    CalendarComponent,
    LoginComponent,
    BuyComponent
  ],
  imports: [
    BrowserModule,
    SharedModule,
    AppRoutingModule,
    BrowserAnimationsModule
  ],
  providers: [AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }
