import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { ConfirmPurchaseDialog } from './dialog/confirm-purchase-dialog';


@Component({
  selector: 'app-buy',
  templateUrl: './buy.component.html',
  styleUrls: ['./buy.component.scss']
})
export class BuyComponent implements OnInit {

  public selectedValue: string = 'R$ 20,00';
  public values: string[] = ['R$ 20,00', 'R$ 50,00', 'R$ 100,00'];
  horizontalPosition: MatSnackBarHorizontalPosition = 'start';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';

  constructor(private _snackBar: MatSnackBar,private router: Router, public dialog: MatDialog) { }

  ngOnInit(): void {
  }

  public buy() {

      const dialogRef = this.dialog.open(ConfirmPurchaseDialog, {
        width: '250px',
        data: { name: 'Guilherme', value: this.selectedValue }
      });
  
      dialogRef.afterClosed().subscribe(result => {
        console.log(result);
        if(result === true) {
          this._snackBar.open('Solicitação realizada com sucesso!', '', {
            duration: 1500,
            horizontalPosition: this.horizontalPosition,
            verticalPosition: this.verticalPosition,
            panelClass: ['blue-snackbar']
          });
  
          this.router.navigateByUrl('/user');

        }
      });
      
  }
    

};
